<template>
<div>
<div v-if="showLogin" class="centered-container">
		<md-content class="md-elevation-4" style="padding: 20px 20px;margin-top:160px">
    	<div class="title">
      	<div class="md-title">Geospoc Admin</div>
      </div>

      <div class="form">
        <md-field>
          <label>E-mail</label>
          <md-input v-model="login.email" autofocus></md-input>
        </md-field>

        <md-field md-has-password>
          <label>Password</label>
          <md-input v-model="login.password" type="password" v-on:keyup.enter="auth"></md-input>
        </md-field>
      </div>

      <div class="actions md-layout md-alignment-center-space-between">
      	<md-button class="md-raised md-primary" @click="auth">Log in</md-button>
      </div>

      <div class="loading-overlay" v-if="loading">
        <md-progress-spinner md-mode="indeterminate" :md-stroke="2"></md-progress-spinner>
      </div>
      <div v-if="showMessage">
      	<p class="red">{{message}}</p>
      	<div v-if="message=='No Email Found'">
      		<md-button @click="requestForApproval()"class="md-raised md-primary">Request for approval</md-button>
      	</div>
      </div>
      <div v-else>
      </div>

    </md-content>
    <md-snackbar :md-position="position" :md-duration="isInfinity ? Infinity : duration" :md-active.sync="showSnackbar" md-persistent>
      <span>{{error}}</span>
    </md-snackbar>
	</div>

	<div v-if="!showLogin">
		<div class="showAllProfiles">
			<h1>Here we will see all the details of the profiles</h1>
			<md-card class="card" v-for="p in profiles">
      <md-card-header>
        <div class="md-title" style="text-align: left;">{{p.name}}
        	<div style="float:right;text-align: right;"><star-rating v-bind:star-size="20" :rating="getRating(p)" :read-only="true"></star-rating>
        	</div>
        </div>
        <div class="md-subhead" style="text-align: left;">{{p.email}}</div>
      </md-card-header>
      <md-card-expand>
      </md-card-expand>
				<md-card-content>
					<div v-if="p.reviews!=null">
						<p style="text-align: left;">Comments</p>
        		<div v-for="review in p.reviews" style="text-align: left;width:50%;">
        			<b>{{review.by}}</b>:{{review.content}}<br>

        		</div>
        	</div>
        	<div class="comment" style="text-align: left;">
        		<br>
        		<input :id="p.email"></input>
        		<md-button @click="addComment(p.email)">Add Comment</md-button>
        	</div>
        	<div class="rate" style="height:100px">

        		<md-button @click="addRating(p.email)" style="float: left;">Add Rating</md-button>
        	</div>
				</md-card-content>
			</md-card>
		</div>
	</div>
</div>
</template>
<script>
	import StarRating from 'vue-star-rating'
export default {
  name: "App",
  components: {
  StarRating
},
props: {
    'name': String,
    'value': null,
    'id': String,
    'disabled': Boolean,
    'required': Boolean
  },
  data() {
    return {
    	rating:0,
    	loggedInEmail:'',
    	showLogin:true,
    	api_url:'api',
      loading: false,
      login: {
        email: "",
        password: ""
      },
      isPresent:false,
      showMessage:false,
      message:'',
      showSnackbar:false,
      error:'',
      position: 'center',
    duration: 4000,
    isInfinity: false,
    profiles:[],

    };
  },
  methods: {


  	validateEmail:function(mail)
    {
    if(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
    {
      return (true)
    }
    return (false)
    },
  	requestForApproval:function(){
  		//First check if this is an email id
  		if (!this.validateEmail(this.login.email)){
          this.error="Email not correct";
          this.showSnackbar=true;
          return;
        }
  		const url = this.api_url+'/approval/'+this.login.email+'/'+this.login.password;
  		this.axios.get(url).then((response) => {
      if(response.data.Status==true){
      	this.showMessage=false;
      	this.message='';
      	this.showLogin=false;
      	this.loggedInEmail=this.login.email;
      	//this.getAllProfiles();

      	this.$router.push({ name: 'admin', params: {email: this.loggedInEmail }})
      	//Logged in
      }else{
      	this.error=response.data.Message;
      	this.showSnackbar=true;
      }
      this.loading = false;

})

  	},
    auth:function() {
      // your code to login user
      // this is only for example of loading
      this.loading = true;
      var _this=this;
      const url = this.api_url+'/checkUser/'+this.login.email+'/'+this.login.password;
      this.axios.get(url).then((response) => {
      if (response.data.Status==false){
      	this.showMessage=true;
      	this.message=response.data.Message;

      }else{
      	this.showMessage=false;
      	this.showLogin=false;
      	this.loggedInEmail=this.login.email;
      	//this.getAllProfiles();
      	this.$router.push({ name: 'admin', params: {email: this.loggedInEmail }})

      	//this.$router.push('/profiles');
      }
      this.loading = false;

})
    }
  }
};
</script>
